import React, { useState, useEffect } from "react";
import HeaderContainer from './HeaderContainer.js'
import MainContainer from './MainContainer.js'
import SettingsContainer from './SettingsContainer.js'
import Footer from './Footer.js'
import prepareHourlyData from '../utils/prepareHourlyData.js'

function App() {

    function fetchData(latitude, longitude, forecast, setDataFunction) {
        var url = 'https://api.open-meteo.com/v1/forecast?latitude=' + latitude + '&longitude=' + longitude
            + '&hourly=temperature_2m,weathercode&daily=weathercode,temperature_2m_max,temperature_2m_min&timezone=Europe%2FBerlin'
        // var url = 'http://localhost:8000/' + forecast
        fetch(url)
        .then(response => {
            return response.json()
        })
        .then(data => {
            data.hourly = prepareHourlyData(data.hourly)
            setDataFunction(data)
            setCurrentForecast(data)
            console.log('faktyczny fetch ' + forecast)
        })
    }

    function showError(error){
        function showMessage(message) {
            setLoadingData(false)
            setErrorDetected({
                'state': true, 
                'message': message
            })
        }
        switch(error.code){
            case error.PERMISSION_DENIED:
                showMessage('Brak zgody użytkownika na udostępnienie lokalizacji')
            break
            case error.POSITION_UNAVAILABLE:
                showMessage('Informacja o lokalizacji jest niedostępna')
            break
            default:
                showMessage('Wystąpił inny błąd')
            break
        }
    }

    const [settings, changeSettings] = useState({
        'noIcons': false,
        'darkMode': window.matchMedia('(prefers-color-scheme: dark)').matches,
    })
    const [currentForecast, setCurrentForecast] = useState(null)
    const [defaultForecast, setDefaultForecast] = useState(null)
    const [yourLocalizationForecast, setYourLocalizationForecast] = useState(null)
    const [currentLocalization, setCurrentLocalization] = useState('default')
    const [loadingData, setLoadingData] = useState(false)
    const [errorDetected, setErrorDetected] = useState({
        'state': false,
        'message': '',
    })

    // fetch default forecast data
    useEffect(() => {
        fetchData(50.05, 19.95, 'forecast1', setDefaultForecast)
    },[])

    // Display correct dataset
    useEffect(() => {
        function showPosition(position) {
            fetchData(position.coords.latitude, position.coords.longitude, 'forecast2', setYourLocalizationForecast)
            setLoadingData(false)
            setErrorDetected({'state': false})
        }

        if (currentLocalization === 'default') {
            if (currentLocalization !== null && defaultForecast !== null) {
                setCurrentForecast(defaultForecast)
            }
        }
        else {
            // Fetch data if not exist
            if (yourLocalizationForecast === null) {
                setLoadingData(true)
                navigator.geolocation.getCurrentPosition(showPosition, showError)
            }
            else {
                setCurrentForecast(yourLocalizationForecast)
            }
        }
    // Only currentLocalization needed, rest is here because of warning
    }, [currentLocalization, defaultForecast, yourLocalizationForecast])

    console.log('reload')

    return (
        <div className={settings.darkMode ? "dark" : ""}>
            <div className={"min-h-screen bg-sky-200 dark:bg-stone-800 dark:text-[#e6ebed] select-none "
                    + (loadingData ? 'brightness-50' : false)}>
                <HeaderContainer
                    currentLocalization={currentLocalization}
                    setCurrentLocalization={setCurrentLocalization}
                    loadingData={loadingData}
                />
                {currentForecast !== null ?
                <MainContainer 
                    hourlyData={currentForecast.hourly}
                    dailyData={currentForecast.daily}
                    settings={settings}
                    errorDetected={errorDetected}
                    currentLocalization={currentLocalization}
                />
                : false}
                <SettingsContainer
                    settings={settings}
                    changeSettings={changeSettings}
                />
                <Footer />
            </div>
        </div>
    )
}

export default App;
